import { get, ref, set } from '@firebase/database';
import BookmarkAddedIcon from '@mui/icons-material/BookmarkAdded';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Fab from '@mui/material/Fab';
import Typography from '@mui/material/Typography';
import React, { Suspense, useState } from 'react';
import { useLocation } from 'react-router-dom';
import AnimateButton from '../../components/button/AnimateButton';
import { firebaseDB } from '../../firebase/firebase';

import { Grid, useTheme } from '@mui/material';
import { useQuery, useSuspenseQuery } from '@tanstack/react-query';
import { LinkItem, TextItem } from '../../components/item/LinkItem';
import { Loading } from '../../components/Loading';
import { ScrollToTop } from '../../components/screen/ScrollToTop';
import { TitleItem } from '../../components/item/TitleItem';
import { useFireUser } from '../../firebase/useFireUser';
import '../MemoDetail.css';
import BibleBookmarkText from '../../components/BibleBookmarkText';

const BtnDisplay = (props) => {
  return (
    <Box sx={{ my: 2, display: 'flex' }}>
      <AnimateButton>
        <Button
          type="button"
          variant="outlined"
          // color="success"
          onClick={props.onBookmark}
        >
          {props.bookmark === 'Y' ? (
            <BookmarkAddedIcon sx={{ color: 'red' }} />
          ) : (
            <BookmarkBorderIcon />
          )}
          &nbsp;북마크
        </Button>
      </AnimateButton>
    </Box>
  );
};

// songList: {cd1: '3', cd2: '1', nm: '1. 만복의 근원 하나님', key: '3_1'}
const SongList = (props) => {
  // const data = new Array();

  if (props.songList === undefined || props.songList === null) return;

  // console.log('SongList=', props.songList.length, props.songList);

  return (
    <Grid container spacing={2} columns={{ xs: 12, md: 12 }}>
      {props.songList.map((item, idx) => {
        return (
          <Grid item xs={12} md={6} key={idx}>
            {(idx + 1) % 100 === 0 && <div id={'song' + (idx + 1)}></div>}
            <LinkItem to={`/bible/song/${idx + 1}`}>{item.nm}</LinkItem>
          </Grid>
        );
      })}
    </Grid>
  );
};

const Song = (props) => {
  // const [songList, setSongList] = useState(null);
  const [bookmark, setBookmark] = useState('N');

  const {
    data: songList,
    isPending,
    isFetching,
  } = useQuery({
    queryKey: ['songList'],
    queryFn: () => getSongList(),
  });

  const theme = useTheme();

  const location = useLocation();
  // console.log('MemoDetail.fullIndex=', fullIndex);

  const { user } = useFireUser();
  // const [memoId, setMemoId] = useState(id);

  // ** Hooks
  // const { settings, saveSettings } = useSettings();

  // useEffect(() => {
  //   getSongList();
  // }, []);

  //console.log('MemoDetail.memoId=', memoId);

  async function getSongList() {
    //찬송가
    const songRef = ref(firebaseDB, 'bible_song');
    const songSnap = await get(songRef);

    const songData = new Array();
    for (let key in songSnap.val()) {
      const data = songSnap.val()[key];
      data.key = data.cd1 + '_' + data.cd2;
      // console.log('songSnap', key, data);
      songData.push(data);

      // 임시 제목 변경용
      // set(ref(firebaseDB, `bible_song_txt/${data.cd2}/nm`), data.nm);
    }
    return songData;
  } // end of getSongList

  const onMove = (event, id) => {
    const anchor = (event.target.ownerDocument || document).querySelector(id);

    if (anchor) {
      anchor.scrollIntoView({
        block: 'center',
        behavior: 'smooth',
      });
    }
  };

  return (
    <React.Fragment>
      <BibleBookmarkText />
      <TitleItem>찬송가</TitleItem>
      <Box
        sx={{
          position: 'fixed',
          right: '10px',
          top: '33%',
          paddingTop: 2,
          paddingBottom: 2,
          paddingLeft: 3,
          paddingRight: 3,
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
          bgcolor: (theme) =>
            theme.palette.mode === 'dark' ? '#101010' : 'grey.100',
        }}
      >
        <Typography
          variant="h7"
          sx={{
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
            mb: 1,
          }}
        >
          page
        </Typography>
        <Box
          sx={{
            paddingTop: 2,
            paddingBottom: 2,
            paddingLeft: 3,
            paddingRight: 3,
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
            border: 1,
            borderRadius: '16px',
            bgcolor: (theme) =>
              theme.palette.mode === 'dark' ? '#101010' : 'grey.100',
          }}
        >
          <TextItem onClick={(e) => onMove(e, '#song100')}>100</TextItem>
          <TextItem onClick={(e) => onMove(e, '#song200')}>200</TextItem>
          <TextItem onClick={(e) => onMove(e, '#song300')}>300</TextItem>
          <TextItem onClick={(e) => onMove(e, '#song400')}>400</TextItem>
          <TextItem onClick={(e) => onMove(e, '#song500')}>500</TextItem>
          <TextItem onClick={(e) => onMove(e, '#song600')}>600</TextItem>
        </Box>
      </Box>
      <SongList songList={songList} />
      {/* <Container sx={{ p: 0 }}>{isLoading && <Loading />}</Container> */}
    </React.Fragment>
  );
};

export default Song;
