import { Divider, Grid } from '@mui/material';
import { HtmlText } from './item/HtmlText';
import { LinkItem } from './item/LinkItem';
import { useBibleBookmark } from '../firebase/useBibleBookmark';

const BibleBookmarkText = () => {
  // const data = new Array();
  const { query } = useBibleBookmark();
  const data = query.data;

  // console.log('BibleText.dataText=', props.dataText);
  // console.log('BibleBookmarkText.data=', data);

  if (data === undefined || data === null) return;

  return (
    <>
      {data.map((item, idx) => {
        // console.log('BibleText.item=', item);
        return (
          <Grid
            container
            spacing={2}
            columns={{ xs: 5, sm: 7, md: 10, lg: 12 }}
            key={idx}
            id={`id_${idx + 1}`}
          >
            <Grid item xs={1} md={1}>
              <LinkItem
                to={`/bible/${item.cd1}_${item.cd2}_${item.cd3}`}
                state={{ cd4: item.cd4 }}
              >
                {item.nm}
              </LinkItem>
            </Grid>
            <Grid item xs={4} sm={6} md={9} lg={11}>
              <HtmlText show={true} text={item.txt} />
            </Grid>
          </Grid>
        );
      })}
      <Divider />
    </>
  );
};

export default BibleBookmarkText;
