import { get, onValue, ref, set } from 'firebase/database';
import { useEffect, useState } from 'react';
import { firebaseDB } from './firebase';
import { useFireUser } from './useFireUser';
import { Query, QueryClient, useQuery, useSuspenseQuery } from '@tanstack/react-query';

// 북마크 관련
// use :
// const bookmark = useBibleBookmark();
export const useBibleBookmark = () => {
  const queryClient = new QueryClient();
  const { user } = useFireUser();

  // const queryClient = new QueryClient();
  // const id = queryClient.getQueryData('userId');
  // console.log('useBibleBookmark.id=', id);
  // let userId; // 'I3cTgdCxwjhBBno4mtZYvnODPJu1';

  const query = useQuery({ // useQuery //useSuspenseQuery
    queryKey: ['useBibleBookmark'],
    // queryFn: () => new Promise(() => {getBookmarkData()}),
    queryFn: ()=> getBookmarkData(),
    // The query will not execute until the userId exists
    enabled: false,
  });

  useEffect(() => {
    if(user !== undefined) {
      // console.log('useEffect.user=', user);
      query.refetch();
    }
  }, [user]);

  async function getBookmarkData() {
    // console.log('getBookmarkData.user', user.uid);
    if (user.uid === undefined) {
      return new Error('Error undefined');
    }
    const locate = ref(firebaseDB, `bible_mark/${user.uid}`);

    const snapshot = await get(locate);
    const result = new Array();
    // console.log('useBookmark.snapshot', snapshot);

    if (!snapshot.exists()) {
      // console.log('getBookmarkData.exists', snapshot);
      return new Error('Error exists');
    } else {
      const data = snapshot.val();
      // console.log('useBookmark.data=', data);
      Object.values(data).map((item) => {
        item.bookmark === 'Y' && result.push(item);
      });
      // console.log('getBookmarkData.result=', result);
      return result;
    }
  }

  // song_mark
  // bible_mark
  const setBibleBookmark = (item) => {
    if (user === undefined) return;

    /* 
    {nm: '잠31:2', text: '내 아들아 내가 무엇을 말하랴 내 태에서 난 아들아 내가 무엇을 말하랴 서원대로 얻은 아들아 내가 무엇을 말하랴', 
    key: '1_20', cd1: '1', cd2: '20', …}
    */

    if (user === undefined) return;

    let value = 'N';
    if (item.bookmark === undefined || item.bookmark === 'N') {
      value = 'Y';
    }
    item.bookmark = value;

    // console.log('setBibleBookmark.item=', item);

    const locate = `bible_mark/${user.uid}/${item.cd1}_${item.cd2}_${item.cd3}_${item.cd4}`;
    set(ref(firebaseDB, locate), item);

    queryClient.invalidateQueries({ queryKey: ['useBibleBookmark'] })
    query.refetch();

    /*     const val = bookmark === 'Y' ? 'N' : 'Y';
    setBookmark(val);

    */
  }

  // console.log('useBibleBookmark.query.data=', query.data);
  return {query, setBibleBookmark};
  //return query
}; // end of useBibleBookmark
